<template>
<q-page padding class="row justify">
  <div class="doc-container">
   <span  v-html.safe="tblCurCase[curModIdx].ModuleNodeText"> </span>
    <div class="row justify-center">  
      <q-list style="width:80vw; max-width: 90vw;" inset-separator>
         <q-list-header>Каким будет ваш ответ?</q-list-header>
          <q-item link tag="label"  v-for="oneVar in tblCurCase[curModIdx].ModuleNodeParams" :key="oneVar.ActionIdx" >
            <q-item-side>
              <q-radio v-model="selOpt" :val="oneVar.ActionIdx"/>
            </q-item-side>
            <q-item-main class="CaseAnswerItem" :label="oneVar.ActionText" />
          </q-item>
      </q-list>
    </div>

    <div class="text-center q-pt-lg" >
      <q-btn color="primary" size="lg"   @click="makeStep"  label="Далее" />
      <p>Class </p>
      arrAnswers {{arrAnswers}}
     selOpt  {{selOpt}} 
     curModIdx {{curModIdx}}
     curQstrId {{curQstrId}} 
     src {{src}}

     ingng {{ingng}}
    </div>

</div>

</div>
</div>

</q-page>  
</template>
<script>

import tableData from 'assets/case-data'
import tblCurCase from 'assets/CaseParameters'
//https://laracasts.com/discuss/channels/vue/using-vue-resource-to-read-xml-file
//d:\Projects\GTiBE\MainPortalApp\quasar-play-dev\src\pages\showcase\grouping\table\table-server-pagination.vue


export default {
  data () {
    return {  
      src: '',  
      selOpt: '',
      curModIdx:0,
      arrAnswers: [],
      curQNum:1,
      curQstrId:'',
      itogBall:0,
      tblCurCase       
    } 
  },
      methods: {
        makeStep(){         
            if (this.selOpt=='') {            
              this.notify();
            }else{
            this.src =  tblCurCase[curModIdx].ModuleNodeParams

                             this.curQstrId = 'app1D'+this.curQNum;
          var picked = tblCurCase.find(o => o.ModuleNodeId === this.curQstrId);
          this.arrAnswers[this.curModIdx]=this.selOpt;
         // this.src = 'picked :'+picked.ModuleNodeIndex+' - '+picked.ModuleNodeId ;

              //this.src = 'dsfgdfg :'+this.selOpt;
              this.selOpt='';
              this.curModIdx++;
            }
            },
       notify () {    
              this.$q.notify({
                  message: 'Не выбран ответ',
                  detail: 'Для продолжения упражнения необходимо выбрать вариант ответа',
                  timeout: 2000, // in milliseconds; 0 means no timeout

                  // "type" adds a color and icon,
                  // so you don't need to specify them.
                  // Available values: 'positive', 'negative', 'warning', 'info'
                  type: 'warning',

                  color: 'positive',
                  textColor: 'black', // if default 'white' doesn't fits
                  icon: 'perm_identity',                  
                  position: 'bottom' // 'top', 'left', 'bottom-left' etc
               })
              }

      }
}
</script>

<style lang="stylus">
@import '~variables'
.CaseDialog1
  padding-bottom 20px
  padding-left 0px
  font-style normal 
.CaseDialog2
  padding-bottom 20px
  padding-left 100px
  font-style italic
  font-weight bold

.CaseAnswerItem  
  font-style italic  
  font-weight 400  
</style>  


